.Sidecontent {
    position: relative;
    padding: 3em 2em;
    min-height: 100vh;
}

.quicklaunch ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.games li img {
    width: 200px;
    border-radius: 5px;
}

.shadow {
    position: fixed;
    content: '';
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 15;
    cursor: pointer;
}

.quicklaunch {
    margin-bottom: 4em;
    position: sticky;
    top: 0px;
    background-color: var(--bg-color1);
    z-index: 10;
}

@media only screen and (max-width: 968px) {
    .Sidecontent {
        margin: auto;
    }
}